import ticketItem from '../../components/ticket-item'
import tkiQrcode from "../../components/tki-qrcode"
export default {
    data() {
        return {
            orderId: '',
            detailArr: [],
            detailObj: null,
            orderType: '0',
            QRStr: '',
            src: ''
        }
    },
    components: {
        ticketItem,
        tkiQrcode
    },
    computed: {
        leftNum() {
            let temp = 0
            this.detailArr.forEach(ele => {
                if (!ele.binding) {
                    temp ++
                }
            })
            return temp
        }
    },
    onLoad(option){
        this.orderId = option.orderId
        this.orderType = option.orderType
    },
    created() {
        this.$nextTick(() => {            
            this.getOrderDetail()
        })
    },
    methods: {
        qrR(res) {
            this.src = res
        },         
        creatQrcode() {
            this.changeQR()
            this.$nextTick(() => {                    
                this.$refs.orderQrcode && this.$refs.orderQrcode._makeCode()
            })
        },
        intoVenueDetail () {
            uni.navigateTo({
                url: `/pages/venueDetail/venueDetail?id=${this.detailObj.specialItemVenueId}`
            })
        },
        async getOrderDetail () {
            let params = {
                orderId : this.orderId
            }
            let res = await this.$api.getOrderDetail(params)
            if (res.code === '0') {
                this.detailObj = JSON.parse(JSON.stringify(res.data))
                this.detailArr = JSON.parse(JSON.stringify(res.data.ticketDOList))
                this.creatQrcode()
            }
        },
        changeQR () {   
            if (this.QRStr) {
                this.$refs.orderQrcode._clearCode()         
            }
            let obj = {
                orderNum: this.detailObj.orderNum
            }
            this.QRStr = JSON.stringify(obj)
        },
    },
}
